<template>
  <b-button
    :class="`btn-score ${this.class}`"
    variant="outline-secondary"
    :title="this.$t('actions.title.score')"
    @click="toggleScore"
  >
    {{ item.score.value }}
  </b-button>

  <b-modal
    v-model="showScore"
    class="score-info"
    ok-only
    :ok-title="$t('buttons.close')"
    :title="item.text"
    ref="modal"
  >
    <dl class="score-info">
      <div
        class="score-info-item"
        v-for="type in item.score.info"
        :key="type.name"
      >
        <dt>{{ $t(`actions.title.${ type.name }`) }}</dt>
        <dd>{{ type.value }}</dd>
      </div>
      <div class="score-info-summary">
        <dd>{{ item.score.value }}</dd>
      </div>
    </dl>
  </b-modal>
</template>

<script>
export default {
  name: 'Score Info',
  props: {
    item: Object,
    class: String,
  },
  data() {
    return {
      showScore: false,
    };
  },
  methods: {
    toggleScore() {
      this.showScore = !this.showScore;
      if (!this.showScore) return;

      window.setTimeout(() => {
        this.$refs.modal.$refs.element.focus();
      }, 300);
    },
  },
};
</script>
