<template>
  <div
    class="dock-display"
    ref="display"
    :ondragover="onDragover"
    :ondrop="onDrop"
  ></div>
  <div
    class="dock-stock"
    ref="stock"
    :ondragover="onDragover"
    :ondrop="onDrop"
  >
    <span
      class="dock-van draggable"
      draggable="true"
      :ondragstart="onDragstart"
      :ondragend="onDragend"

      @click="onClick"

      v-for="(van, i) in breakingText"
      :key="i"
      :ref="van.code"
      >{{ van.text }}</span
    >
  </div>
</template>

<script>
export default {
  name: 'TrianingByDocking',
  inject: ['buttons'],
  props: { item: Object },
  data() {
    return {
      isSuccess: false,
      isScored: false,
    };
  },
  computed: {
    docks() {
      let docks = this.item.text.split('');
      const complexity = this.$store.getters.settings?.training.complexity.docking;

      // by default by letters
      if (!complexity || complexity === 1.2) return docks;

      const easiest = complexity === 0.4;
      const separator = '';

      // split by spaces for easiest
      if (this.item.textType !== 'word' && easiest) {
        return this.item.text
          .split(' ')
          .join(`${separator} ${separator}`)
          .split(separator);
      }

      const maxLenght = docks.length;
      let lengthDock;
      if (maxLenght < 7) {
        lengthDock = easiest ? 3 : 2;
      } else if (maxLenght < 9) {
        lengthDock = easiest ? 4 : 3;
      } else {
        lengthDock = easiest ? 5 : 4;
      }

      const countDocks = Math.ceil(maxLenght / lengthDock);
      const posArray = new Array(countDocks).fill(lengthDock);
      let surplus = countDocks * lengthDock - maxLenght;
      for (let i = 0; i < countDocks && surplus > 0; i++) {
        if (posArray[i] === 1) continue;
        --posArray[i];
        --surplus;
      }
      posArray.sort(() => 0.5 - Math.random());

      for (let i = 0, pos = 0; i < countDocks - 1; i++) {
        pos += posArray[i];
        docks.splice(pos + 1 * i, 0, separator);
      }

      return docks.join('').split(separator);
    },
    breakingText() {
      const docks = this.docks;
      return docks
        .sort(() => 0.5 - Math.random())
        .map((text) => {
          return {
            text,
            code: `${text[0].toLowerCase()}`,
          };
        });
    },
  },
  watch: {
    breakingText() {
      this.clear();
    },
    item() {
      this.clear();
    },
    'buttons.right.pressed'() {
      this.exam();
    },
  },
  mounted() {
    this.clear();

    window.addEventListener('keyup', this.onKeyup);
    window.addEventListener('keydown', this.onKeydown);
  },
  unmounted() {
    window.removeEventListener('keyup', this.onKeyup);
    window.removeEventListener('keydown', this.onKeydown);
    this.$store.commit('loaded', { currentTraining: undefined });
  },
  methods: {
    clear() {
      this.$store.commit('loaded', { currentTraining: false });
      this.$refs.stock.append(...this.$refs.display.children);
      this.isSuccess = false;
      this.isScored = false;
      this.$store.commit('loaded', { currentTraining: true });
      this.onSuccess();
    },
    check() {
      this.isSuccess = this.$refs.display.textContent === this.item.text;
      this.onSuccess();
    },
    onSuccess() {
      this.$refs.display.classList.toggle('training-success', this.isSuccess);
      this.$store.commit('success', this.isSuccess);
      if (!this.isSuccess) return;

      this.$speak(this.item);
      this.exam();
    },
    exam() {
      if (this.isScored) return;
      this.item.score.exam({
        name: 'docking',
        result: this.isSuccess ? 'success' : 'skip',
      });
      this.isScored = true;
    },

    onClick(e) {
      const btn = e.currentTarget;
      const parent = btn.parentElement;
      const { display, stock } = this.$refs;
      const target = parent === display ? stock : display;

      target.appendChild(btn);
      this.check();
    },

    onKeydown(e) {
      if (e.code !== 'Space') return;
      e.preventDefault();
    },
    onKeyup(e) {
      const display = this.$refs.display;
      const key = e.key.toLowerCase();
      let target;

      switch (key) {
        case 'backspace':
          target = display.lastElementChild;
          break;
        default:
          target = this.$refs[key]?.find(
            (van) => van.parentElement !== display
          );
      }

      target && this.onClick({
          currentTarget: target,
        });
    },

    onDragstart(e) {
      e.target.classList.add('grabbing');
    },
    onDragend(e) {
      e.target.classList.remove('grabbing');
    },
    onDragover(e) {
      e.preventDefault();
      const futureParent = e.currentTarget;
      const draggableElement = futureParent.parentElement.querySelector('.grabbing');
      let currentTarget = e.target;

      if (draggableElement === currentTarget) return;
      if (currentTarget !== futureParent) {
        currentTarget = currentTarget.closest('.draggable');
      }

      const futureSibling =
        currentTarget === draggableElement.nextElementSibling
          ? currentTarget.nextElementSibling
          : currentTarget;

      if (futureSibling && futureSibling !== futureParent) {
        futureParent.insertBefore(draggableElement, futureSibling);
      } else {
        futureParent.appendChild(draggableElement);
      }
    },
    onDrop() {
      this.check();
    },
  },
};
</script>
