export default {
  namespaced: false,
  state: {
    alerts: {}, //** { timestamp: {name: String, icon: String} }
  },
  getters: {
    alerts: (state) => state.alerts, // fix: remove!
    alert: (state) => {
      const timestamps = Object.values(state.alerts)
        .map((value) => Object.keys(value))
        .flat();
      if (!timestamps.length) return;

      const last = Math.max(...timestamps);
      const index = timestamps.indexOf(String(last));
      const name = Object.keys(state.alerts)[index];
      const data = state.alerts[name][last];

      return {
        name,
        data: typeof data === 'boolean' ? {} : data,
        icon: {
          noneNetwork: 'material-symbols:network-manage',
          noneItems: 'material-symbols:settings-alert-outline',
          nonePicking: 'material-symbols:settings-alert-outline',
          noneArticle: 'material-symbols:data-alert',
          emptyCollection: 'material-symbols:settings-alert-outline',
        }[name],
      };
    },
  },
  mutations: {
    alert: (state, item) => {
      Object.keys(item).forEach((key) => {
        if (item[key]) {
          state.alerts[key] = {
            [Date.now()]: item[key],
          };
        } else {
          delete state.alerts[key];
        }
      });
    },
  },
};
