import { getIndexedDB } from '../utils/indexeddb';
import { i18n } from '@/i18n';

export default {
  namespaced: false,
  state: {
    collections: [],
    collection: null,
    indexedDB: null,
    readyState: 0,
  },
  getters: {
    collections: (state) => state.collections,
    collection: (state) => state.collection,
  },
  mutations: {
    collections(state, collections) {
      state.collections = collections;
      this.dispatch('syncCollectionsDB');
      this.commit('collectionID', this.getters.settings.collectionID);
    },
    collectionID(state, collectionID) {
      if (!state.collections.length) return;
      if (state.collection?.id === collectionID) return;

      state.collection = state.collections.find(
        (collection) => collection.id === collectionID
      );
      this.dispatch('fetchItems');
    },
  },
  actions: {
    async fetchCollections({ commit, dispatch, state }, options = {}) {
      const { force } = options;
      if (!(force || state.readyState === 0)) return;

      state.readyState = 1;
      const sets = []; // [indexeddb, def, mongodb]
      return dispatch('fetchCollectionsDB')
        .then(function (e) {
          e && sets.push(e.target.result);
        })
        .catch(function (error) {
          console.info('Only session default collections will be used.\n', error);
        })
        .finally(() => {
          sets.push(
            require(process.env.VUE_APP_PATH_TO_COLLECTIONS).collections
          );
          const ids = {};
          const collections = sets.flat().filter((item) => {
            if (ids[item.id]) return false;
            ids[item.id] = true;
            if (!item.name) Object.defineProperty(item, 'name', {
                get: () => i18n.t(`collections.name.${item.id}`),
              });
            return true;
          });

          state.readyState = 4;
          commit('collections', collections);
        });
    },
    async fetchCollectionsDB({ state }) {
      state.indexedDB = getIndexedDB();
      return state.indexedDB.then(function (db) {
        return db.store('collections').get();
      });
    },
    async syncCollectionsDB({ state }, collections) {
      collections ||= state.collections;
      if (!state.indexedDB || !collections) return;

      return state.indexedDB.then((db) => {
        return db.store('collections').set(collections);
      });
    },
  },
}