<template>
  <div class="digital-count">
    <iconify-icon
      v-for="(digital, index) in digitals"
      :key="index"
      :icon="`fad:digital${digital}`"
    ></iconify-icon>
  </div>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'DigitalCount',
  components: {
    IconifyIcon,
  },
  props: {
    count: Number,
    lengthBy: { type: Number, default: 0 },
  },
  computed: {
    digitals() {
      const length = String(this.lengthBy).length;
      const str = Math.round(this.count).toString();
      const leadingZeros = length - str.length;
      const digitals = leadingZeros > 0 ? new Array(leadingZeros).fill('0') : [];
      digitals.push(...str.split(''));
      return digitals;
    },
  },
};
</script>
