export const parseBoolean = (value, isDeep) => {
  if (!value) return false;

  if (isDeep) {
    if (Array.isArray(value)) {
      return value.some((i) => parseBoolean(i, isDeep));
    }

    if (typeof value === 'object' && String(value) === '[object Object]') {
      return parseBoolean(Object.values(value), isDeep);
    }
  }

  switch (String(value).trim().toLowerCase()) {
    case '0':
    case 'false':
    case 'null':
    case 'undefined':
      return false;
  }

  return true;
};
