<template>
  <div v-show="!$store.getters.alert" class="training-picking">
    <b-form-radio
      class="hotkey-counter"
      name="pickingItem"

      v-for="(item, index) in pickingItems"
      :key="index"

      :id="item.id"
      :value="index + 1"
      v-model="code"
      :state="valid[index]"

      type="container"
      ref="radio"
    >
      {{ item.text }}
    </b-form-radio>
  </div>
</template>

<script>
export default {
  name: 'TrianingByPicking',
  inject: ['buttons'],
  props: { item: Object },
  data() {
    return {
      isSuccess: false,
      isScored: 0,
      valid: [],
      code: null, // a value can't be 0 to be checked
    };
  },
  computed: {
    complexity() {
      return this.$store.getters.complexity.picking;
    },
    count() {
      return this.getCount(
        this.complexitySettings.picking || this.complexity.default
      );
    },
    pickingItems() {
      return this.$store.getters.pickingItems;
    },
    complexitySettings() {
      return this.$store.getters.settings.training.complexity;
    },
  },
  watch: {
    count() {
      this.clear();
      this.update();
    },
    item() {
      this.clear();
      this.update();
    },
    'buttons.right.pressed'() {
      this.exam('skip');
    },
    // check: update in bulk
    // '$store.getters.settings.training.updated'() {
    //   this.update();
    // },
    '$store.getters.settings.training.lang'() {
      this.update();
    },
    '$store.getters.settings.training.native'() {
      this.update();
    },
    '$store.getters.settings.training.useReverseTranslation'() {
      this.update();
    },
    code() {
      this.check();
    },
  },
  mounted() {
    this.clear();

    window.addEventListener('keyup', this.onKeyup);
  },
  unmounted() {
    window.removeEventListener('keyup', this.onKeyup);
    this.$store.commit('alert', { nonePicking: undefined });
    this.$store.commit('loaded', { currentTraining: undefined });
  },
  created() {
    this.update();
  },
  methods: {
    onKeyup(e) {
      this.code = parseInt(e.key);
    },
    clear() {
      this.isSuccess = false;
      this.isScored = 0;
      this.code = null;
      this.valid = Array(this.count).fill(null);
      this.onSuccess();
    },
    getCount(level) {
      switch (level) {
        case this.complexity.min:     // 0.5
          return 3;
        case this.complexity.default: // 1
          return 5;
        case 1.5:
          return 7;
        case this.complexity.max:     // 2
          return 10;
      }
    },
    async update(isForce) {
      this.$store.commit('loaded', { currentTraining: false });
      await this.$store.commit('pickingItems', {
        count: this.count,
        item: this.item,
      });
      this.$store.commit('loaded', { currentTraining: true });

      const itemsLength = this.pickingItems.length;
      if (itemsLength < this.count) {
        if (this.complexitySettings.picking === this.complexity.min) {
          this.$store.commit('alert', { nonePicking: true });
          if (isForce) {
            this.complexitySettings.picking === this.complexity.default;
          }
        } else if (!isForce) {
          let level;
          for (
            level = this.complexitySettings.picking || this.complexity.default;
            level > this.complexity.min;
            level -= this.complexity.step
          ) {
            if (this.getCount(level) <= itemsLength) break;
          }
          this.complexitySettings.picking = level;
          this.update(true);
        }
        return;
      }
      this.$store.commit('alert', { nonePicking: undefined });
    },
    check() {
      const id = this.$refs.radio[this.code - 1]?.id;
      if (!id) return;

      this.isSuccess = id === this.item.id;
      this.valid[this.code - 1] = this.isSuccess;

      this.isSuccess
        ? this.exam('success')
        : this.exam(
            // reduce the score for the wrong picking of item
            'error',
            this.pickingItems.find((item) => item.id === id)
          );

      this.onSuccess();
    },
    onSuccess() {
      this.$store.commit('success', this.isSuccess);
      if (!this.isSuccess) return;
      this.$speak(this.item);
    },
    exam(result, item = this.item) {
      if (this.isScored === true) return;

      let score = 1 / this.pickingItems.length;
      if (this.isSuccess) {
        score = 1 - this.isScored * score;
        this.isScored = true;
      } else ++this.isScored; // count of errors before isSuccess

      item.score.exam({
        name: 'picking',
        result,
        score: result === 'skip' ? 1 : score,
      });
    },
  },
};
</script>
