<template>
  <template v-for="(name, i) in Object.keys(parts)" :key="i">
    <h5>{{ $t(`info.sections.about.parts.${name}`) }}</h5>

    <ul class="info-item">
      <template v-for="(item, j) in parts[name]" :key="j">
        <li v-if="item">
          <component
            :is="item.link ? 'a' : 'span'"
            :target="item.link && '_blank'"
            :href="item.link"
          >{{ item.name }}</component>{{ item.version ? `: ${item.version}` : '' }}
          {{ item.text ? ` ${item.text}` : '' }}
        </li>
      </template>
    </ul>
  </template>
</template>

<script>
import aboutPackages from '/about-packages.json';
import merge from 'deepmerge';

export default {
  name: 'Info About',
  data() {
    return {
      parts: {
        ui: [
          {
            name: 'mololab/json-translator',
            link: 'https://github.com/mololab/json-translator',
          },
        ],
        comics: [
          {
            name: 'Prisma',
            link: 'https://prisma-ai.com/prisma',
          },
          {
            name: 'Bitmoji',
            link: 'https://www.bitmoji.com/',
          },
        ],
      },
    };
  },
  beforeMount() {
    this.parts = merge.all([{}, aboutPackages, this.parts]);
    Object.keys(this.parts).forEach((key) => this.prepareData(this.parts[key]));
  },
  methods: {
    prepareData(data) {
      return (Array.isArray(data) ? data : Object.keys(data)).map((item) => {
        const isPackage = !!item.name;
        const name = item.name || item;
        const text = this.$tm(`info.about.packages['${name}']`);

        if (!isPackage) {
          item = {
            name,
            link: data[name],
          };
        }
        item.text = typeof text === 'string' && text;

        return item;
      });
    },
  },
};
</script>
