<template>
  <!-- Alert -->
  <dit-alert :params="currentItem"></dit-alert>

  <!-- Spinner -->
  <div v-if="!isLoaded && !alert" class="spinner-container">
    <b-spinner class="loader" variant="warning"></b-spinner>
  </div>

  <!-- Training -->
  <section
    v-show="isLoaded && !alert"
    v-if="currentItem"
    :class="`training training-by-${type} ${isSuccess ? '' : 'un'}success-training`">
    <b-row class="training-title">
      <b-col>
        <article-header :item="currentItem" :title="currentItem.translation" />
      </b-col>
    </b-row>
    <b-row :lang="currentItem.lang">
      <b-col>
        <component :is="`training-by-${type}`" :item="currentItem"></component>
      </b-col>
    </b-row>
  </section>
</template>

<script>
import Article from '@/views/Article/Article';
import ArticleHeader from '@/components/Article/Header';
import DitAlert from '@/components/DitAlert/DitAlert';
import { Icon as IconifyIcon } from '@iconify/vue';
import TrainingByCard from '@/components/Training/Card';
import TrainingByDocking from '@/components/Training/Docking';
import TrainingByPicking from '@/components/Training/Picking';
import TrainingByWriting from '@/components/Training/Writing';

export default {
  name: 'Training',
  components: {
    ArticleHeader,
    DitAlert,
    IconifyIcon,
    'training-by-article': Article,
    'training-by-card': TrainingByCard,
    'training-by-docking': TrainingByDocking,
    'training-by-picking': TrainingByPicking,
    'training-by-writing': TrainingByWriting,
  },
  computed: {
    isSuccess() {
      return this.$store.getters.isSuccess;
    },
    isLoaded() {
      return this.$store.getters.isLoaded;
    },
    alert() {
      return this.$store.getters.alert;
    },
    type() {
      const type = this.$options.components[`training-by-${this.$route.params.type}`]
        ? this.$route.params.type
        : this.$store.getters.trainingType ||
          this.$store.getters.settings?.training.type;

      if (this.$route.meta.active === 'training')
        this.$store.commit('trainingType', type);

      return type;
    },
    currentItem() {
      return this.$store.getters.dictionaryItem || this.$store.getters.trainingItem;
    },
  },
  watch: {
    '$store.getters.itemsLength'() {
      this.update();
    },
    currentItem() {
      this.setTitle();
    },
    type() {
      this.setTitle();
    },
  },
  beforeCreate() {
    this.$store.commit('loaded', {
      training: false,
      currentTraining: false, // set structure
    });
  },
  async created() {
    await this.update();
    this.setTitle();

    this.$watch(
      () => this.$route.params,
      async (newParams, oldParams) => {
        if (!newParams.text || !newParams.type) return;
        if (
          newParams.text === oldParams.text &&
          newParams.type === oldParams.type
        )
          return;
        this.$store.commit('trainingType', this.type);
        await this.update();
      }
    );
  },
  unmounted() {
    this.$store.commit('loaded', { training: undefined });
  },
  methods: {
    async update() {
      if (!this.$store.getters.itemsLength) return; // before dictionary loaded

      this.$store.commit('loaded', { training: false });

      await this.$store.dispatch('dictionaryItem', this.$route.params);

      this.$store.commit('loaded', { training: true });
    },
    setTitle() {
      this.$store.commit('title', () => {
        const currentItem = this.$store.getters.dictionaryItem || this.$store.getters.trainingItem;
        return currentItem
        ? this.$t(`${this.type === 'article' ? this.type : 'training'}.title`, currentItem)
        : this.$t('training.name')
      });
    },
  },
};
</script>
