<template>
  <div class="lang-icon" v-if="show ?? showBySettings">{{ value }}</div>
</template>

<script>
export default {
  name: 'DitLangIcon',
  props: {
    native: String,
    lang: String,
    // by default doesn't show for the training lang from the settings
    show: { type: Boolean, default: null },
  },
  computed: {
    key() {
      return this.native ? 'native' : 'lang';
    },
    value() {
      return this[this.key];
    },
    showBySettings() {
      const trainingLang = this.$store.getters.settings.training[this.key];
      return this.$store.getters[`${this.key}s`].length < 2 || this.value !== trainingLang;
    },
  },
};
</script>
