<template>
  <div class="lookup">
    <section class="lookup-dict" v-for="(dict, name) in dicts" :key="name">
      <template v-if="dict.length">
        <h4 v-if="$t(`dictionary.names.${name}`)">{{ $t(`dictionary.names.${name}`) }}:</h4>
        <div class="text-pos" v-for="(item, i) in dict" :key="`${name}-${item?.pos?.code}-${i}`">
          <b v-if="item.pos" class="text-pos-name">{{ item.pos.text }}</b>
          <em v-if="item.ts" class="transcription">{{ item.ts }}</em>
          <div
            v-for="(tr, j) in item.tr"
            :key="`${name}-${item?.pos?.code}-${tr?.pos?.code}-${i}-${j}`">
            <div v-if="tr.idiom" class="text-idiom">
              <h6 v-if="tr.idiom">{{ $t('dictionary.idiom') }}</h6>
              {{ tr.def }}
            </div>

            <i class="text-tr">
              {{ tr.text }}
            </i>
            <span class="text-tr-extra" v-if="tr.mean">
              <i class="text-tr" v-for="(extra, n) in tr.mean" :key="n">
                {{ extra.text }}
              </i>
            </span>
            <span class="text-tr-extra" v-if="tr.syn">
              <i class="text-tr" v-for="(extra, n) in tr.syn" :key="n">
                {{ extra.text }}
              </i>
            </span>
          </div>
        </div>
      </template>
    </section>
  </div>
</template>

<script>
export default {
  name: 'ArticleLookup',
  props: { item: Object },
  emits: ['shown:article'],
  computed: {
    dicts() {
      return this.$store.getters.lookup;
    },
  },
  watch: {
    dicts(dicts) {
      this.$emit('shown:article', {
        name: 'lookup',
        shown: !!dicts,
      });
    },
  },
};
</script>
