<template>
  <section class="query-corpus">
    <div v-for="example in examples" :key="example.id" class="pairs-both">
      <div class="pairs-first">
        <span v-html="formattingText(example.src)"></span>
        <speech
          size="sm"
          :text="formattingVoice(example.src)"
          :lang="currentItem.lang"
        ></speech>
      </div>
      <div class="pairs-second">
        <span v-html="formattingText(example.dst)"></span>
        <iconify-icon
          :id="`icon-${example.id}`"
          :icon="getSourceIcon(example.ref.type)"
          class="info-icon"
        ></iconify-icon>
        <b-popover
          variant="warning"
          :target="`icon-${example.id}`"
          :title="`${$t('article.source.title')}:`"
          triggers="hover click blur"
        >
          <template v-for="(item, name) in example.ref" :key="name">
            <h4 class="text-end" v-if="name === 'type'">
              {{ $t(`article.source.type.${example.ref.type}`) }}
            </h4>
            <template v-else>
              <dt>{{ $t(`article.source.field.${name}`) }}:</dt>
              <dd>{{ item }}</dd>
            </template>
          </template>
        </b-popover>
      </div>
    </div>
  </section>
</template>

<script>
import Speech from '@/components/Speech/Speech';
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'ArticleQueryCorpus',
  props: { item: Object },
  emits: ['shown:article'],
  components: {
    Speech,
    IconifyIcon,
  },
  computed: {
    examples() {
      return this.$store.getters.queryCorpus?.examples || [];
    },
    currentItem() {
      return this.item || this.$route.params;
    },
  },
  watch: {
    examples(examples) {
      this.$emit('shown:article', {
        name: 'query-corpus',
        shown: !!examples?.length,
      });
    },
  },
  methods: {
    formattingText(text) {
      return text.replace(/<([^>]+)>/g, '<b>$1</b>');
    },
    formattingVoice(text) {
      return '<?xml version="1.0"?>' +
        '<speak version="1.1" xmlns="http://www.w3.org/2001/10/synthesis">' +
        text.replace(/<([^>]+)>/g, '<emphasis level="strong">$1</emphasis>') +
        '</speak>';
    },
    getSourceIcon(type) {
      return (
        {
          book: 'dashicons:book',
          sites: 'iconoir:internet',
          news: 'emojione-monotone:newspaper',
          series: 'pixelarticons:movie',
          movie: 'bx:movie-play',
          IT: 'solar:programming-bold',
        }[type] || 'bi:info-lg'
      );
    },
  },
};
</script>
