<template>
  <div class="training-actions">
    <template v-for="button in buttons" :key="button.name">
      <template v-if="button.icon">
        <b-button
          variant="outline-secondary"
          :to="url(button)"
          :active="isActive(button)"
          :disabled="button.disabled"
          :class="`actions-btn actions-btn-${button.name}`"
          :title="this.$t(`actions.title.${button.name}`)"
        >
          <iconify-icon :icon="button.icon"></iconify-icon>
        </b-button>
      </template>
      <template v-else-if="item">
        <component
          :class="`actions-btn actions-btn-${button.name}`"
          :is="`training-${button.name}`"
          :item="item"
        ></component>
      </template>
    </template>
  </div>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
import TrainingScore from '@/components/Score/Score';

export default {
  name: 'TrainingActions',
  components: {
    IconifyIcon,
    TrainingScore,
  },
  props: {
    item: Object,
    isBulk: Boolean,
    actions: Array,
  },
  data() {
    return {
      default: [
        {
          name: 'card',
          icon: 'ph:cards',
        },
        {
          name: 'picking',
          icon: 'icon-park-outline:list-success',
        },
        {
          name: 'docking',
          icon: 'material-symbols:keyboard-outline',
        },
        {
          name: 'writing',
          icon: 'icon-park-outline:writing-fluently',
        },
        {
          name: 'article',
          icon: 'material-symbols:menu-book-outline-rounded',
        },
        {
          name: 'score',
        },
      ],
    };
  },
  computed: {
    buttons() {
      return this.actions
        ? this.default.filter((btn) => this.actions.indexOf(btn.name) > -1)
        : this.default;
    },
    isRouteItem() {
      const routeParams = this.$route.params;
      return routeParams && this.item
        ? routeParams.id
          ? routeParams.id === this.item.id
          : routeParams.text === this.item.text && routeParams.lang === this.item.lang
        : false;
    },
  },
  methods: {
    url(button) {
      return this.isBulk
        ? `/${button.name}/`
        : `/${button.name}/${this.item.lang}/${this.item.text}/${this.item.id}`;
    },
    isActive(button) {
      return this.isBulk
        ? button.name === this.$store.getters.trainingType
        : this.isRouteItem && this.$route.params.type === button.name; // Dictionary Layout
    },
  },
};
</script>
