<template>
  <div class="comics" ref="comics">
    <svg xmlns="http://www.w3.org/2000/svg" id="defs">
      <defs>
        <image
          :id="data.comp.id"
          :href="`/static/images/history/${data.comp.id}.png`"
          :width="data.comp.width"
          :y="data.comp.y + data.top"
        />
        <image
          v-for="(img, i) in data.images"
          :key="i"
          :id="img.bitmoji"
          class="bitmoji"
          :href="`/static/images/history/bitmoji/${img.bitmoji}.png`"
          :x="data.bitmoji.x"
          :y="data.bitmoji.y + data.top"
          :width="data.bitmoji.size"
        />
      </defs>
    </svg>

    <div
      v-for="(img, i) in data.images"
      :key="i"
      :title="$t(`comics.${i + 1}`)"
      :class="`comics-item comics-item-${img.bitmoji}`"
    >
      <svg
        xmlns="http://www.w3.org/2000/svg"
        :width="data.width"
        :height="data.height"
        :viewBox="`0 0 ${data.width} ${data.height}`"
      >
        <g>
          <use
            :href="`#${img.bitmoji}`"
            :y="!img.comp ? data.height - data.bitmoji.size : data.bitmoji.y + data.top"
          />
          <use v-if="img.comp" href="#comp" />
        </g>
      </svg>
    </div>

  </div>
</template>

<script>
export default {
  name: 'Info Comics',
  data() {
    return this.calcData();
  },
  methods: {
    calcData() {
      const width = screen.width < 910 ? 140 : 170;
      const height = ~~(width * 1.1578);
      const offset = ~~(width / 15);
      const bitmoji = {
        origin: {
          width: 398,
          height: 398,
        },
        size: ~~(width * 2 / 3),
        x: ~~(width - width * 2 / 3)/2 - offset,
        y: 0,
      };
      const comp = {
        id: 'comp',
        origin: {
          width: 398,
          height: 221,
        },
        width,
        y: bitmoji.size - offset,
      };
      comp.height = ~~(comp.origin.height / (comp.origin.width / width));

      this.data = {
        offset,
        width,
        height,
        bitmoji,
        comp,
        top: height - comp.height - bitmoji.size + offset,
        images: [
          { bitmoji: 'happy', comp: true },
          { bitmoji: 'really', comp: true },
          { bitmoji: 'unhappy', comp: true },
          { bitmoji: 'coffee', comp: false },
          { bitmoji: 'angry', comp: true },
          { bitmoji: 'cat', comp: true },
        ],
      };
    }
  }
};
</script>
