import { parseBoolean } from './common.js';

const {
  dbName = 'dicttrain',
  dbVersion = 1,
  dbStore = [
    {
      name: 'records',
      keyPath: 'id',
      indexes: ['collectionID', 'lang', 'native', 'text'],
      data: [],
      queue: [],
    },
    {
      name: 'settings',
      keyPath: 'userID',
      data: [],
      queue: [],
    },
    {
      name: 'collections',
      keyPath: 'id',
      data: [],
      queue: [],
    },
    {
      name: 'i18n',
      keyPath: 'locale',
      data: [],
      queue: [],
    },
  ],
} = {};

const indexedDB =
  window.indexedDB ||
  window.mozIndexedDB ||
  window.webkitIndexedDB ||
  window.msIndexedDB;

let openPromise = null;
const dicttrainDB = {
  idb: null,

  open: async function (skipCheckVersion) {
    if (openPromise) return openPromise;

    const _this = this;
    openPromise = new Promise(function (resolve, reject) {
      if (_this.idb && _this.idb.version >= dbVersion) return resolve(_this);

      const request = indexedDB.open(dbName, skipCheckVersion && dbVersion);
      request.onerror = reject;

      request.onsuccess = function (e) {
        _this.idb = e.target.result;
        const idb = _this.idb;

        idb.onversionchange = function (e) {
          // deleted or changed by other page
          console.warn(
            'There have been changes in IndexedDB. Need to reload the page.'
          );
          idb.close();
          window.navigation.reload();
        };

        if (_this.idb.version >= dbVersion) return resolve(_this);

        // save old data
        Promise.all(
          dbStore.map((params) => {
            return new Promise((resolve) => {
              const { name, deleteData } = params;
              if (!idb.objectStoreNames.contains(name)) return resolve();
              params.data = params.data || [];
              if (deleteData) return resolve();
              const transaction = idb.transaction(name);
              const request = transaction.objectStore(name).getAll();
              request.onerror = reject;
              request.onsuccess = (e) => {
                params.data.push(...e.target.result);
                resolve();
              };
            });
          })
        ).then(() => {
          openPromise = null;
          idb.close();
          return _this.open(true);
        });
      };

      request.onupgradeneeded = function (e) {
        _this.idb = e.target.result;
        const idb = _this.idb;
        let storeTransaction;

        [...idb.objectStoreNames].forEach((name) => idb.deleteObjectStore(name));
        dbStore.forEach((params) => {
          const { name, keyPath, indexes } = params;
          const objectStore = idb.createObjectStore(
            name,
            keyPath ? { keyPath } : { autoIncrement: true }
          );

          indexes &&
            indexes.forEach((index) => {
              const name = index?.name || index;
              objectStore.createIndex(name, name, { unique: index[name] });
            });

          // It's same one for different stories
          storeTransaction = objectStore.transaction;
        });

        storeTransaction.oncomplete = (e) => {
          dbStore.forEach((params) => {
            const { name, data = [], queue = [] } = params;
            const store = idb.transaction(name, 'readwrite').objectStore(name);
            data.forEach((item) => store.add(item));
            params.data = [];

            queue.forEach(([method, data]) => _this.store(name)[method](data));
            params.queue = [];
          });

          resolve(_this);
        };
      };
    });

    return openPromise;
  },

  store: function (name, isWritable) {
    if (!this.idb) return null;

    const objStore = (transaction) => {
      return {
        object: transaction.objectStore(name),
        get: (key) => {
          const storeObject = this.store(name).object;
          return new Promise(function (resolve, reject) {
            let request;
            if (!key) request = storeObject.getAll();
            else if (typeof key === 'object') {
              const indx = Object.keys(key)[0];
              request = storeObject.index(indx).getAll(key[indx]);
            } else request = storeObject.get(key);
            request.onerror = reject;
            request.onsuccess = resolve;
            return request;
          });
        },
        set: (data) => {
          const store = this.store(name, true); // set readwrite
          if (!store) return null;
          data = JSON.parse(JSON.stringify(data)); // clear from overwraps
          const storeObject = store.object;
          // const keyPath = storeObject.keyPath;
          const requests = (Array.isArray(data) ? data : [data]).map(
            (source) => {
              const [item, method = 'put'] = Array.isArray(source)
                ? source
                : [source];
              return new Promise(function (resolve, reject) {
                const requestUpdate = storeObject[method](item);
                requestUpdate.onerror = reject;
                requestUpdate.onsuccess = resolve;
              }); // .catch((e) => console.error(e, item)); // for debug
            }
          );
          return Promise.allSettled(requests).then((requests) => {
            requests.forEach((request) => {
              if (request.status === 'rejected') {
                console.error(request);
              }
            });
            return requests;
          });
        },
      };
    };

    try {
      const transaction = this.idb.transaction(
        name,
        isWritable ? 'readwrite' : 'readonly'
      );
      transaction.onerror = console.error; // todo: ???

      return objStore(transaction);
    } catch (e) {
      // deferred transactions on minimals for onupgradeneeded
      console.warn(e);
      return {
        set: (data) => {
          dbStore.find((item) => item.name === name).queue.push(['set', data]);
        },
      };
    }
  },
};

export async function getIndexedDB() {
  if (!indexedDB) {
    return new Promise(function (resolve, reject) {
      reject('Indexeddb not available.');
    });
  }

  if (parseBoolean(process.env.VUE_APP_INDEXEDDB_DISABLE)) {
    return new Promise(function (resolve, reject) {
      reject('Indexeddb is disabled.');
    });
  }

  return dicttrainDB.open();
}
