<template>
  <div class="dit-parse-json">
    {{ $t('addDictionary.fileParsed') }}
    {{ $t('addDictionary.countRecords', { count: items.length }) }}
  </div>
</template>

<script>
import { getItem } from '@/utils/item.js';

export default {
  name: 'DitParseCsv',
  emits: ['parse:error'],
  props: {
    content: String,
    type: String,
  },
  inject: ['fields', 'valid', 'native', 'collection'],
  data() {
    return {
      items: [],
    };
  },
  watch: {
    content(content) {
      this.items = this.parse(content);
      this.validation();
    },
  },
  methods: {
    parse(content) {
      const items = [];
      if (content) {
        try {
          const json = JSON.parse(content);
          const collection = json.collectionAction;
          let records;
          if (collection) {
            const { id, name } = collection;
            if (id) this.collection.id = id;
            if (name) this.collection.name = name;
            records = collection?.recordsActions || json;
          } else records = json;

          let collectionID = this.collection.id;
          if (Array.isArray(records)) records.forEach((source) => {
            collectionID ||= source.collectionID;
              const item = getItem(source, {
                native: this.native,
                collectionID,
              });
              item && items.push(item);
            });

          if (collectionID) {
            this.collection.id = collectionID;
          }
        } catch (e) {
          console.error(e);
        }
      }
      if (!items.length) {
        this.$emit('parse:error', {
          type: 'parse',
          options: { type: this.type },
        });
      }
      return items;
    },
    validation() {
      if (!this.items.length) return;
      this.fields.forEach((field) => {
        this.valid[field] = true;
      });
    },
    getItems() {
      return this.items;
    },
  },
};
</script>
