<template>
  <!-- Match fields to content cols -->
  <b-form class="dit-parse-csv match-form" v-if="parsed.length">
    <h5>{{ $t('addDictionary.setFields') }}</h5>

    <div class="line-from">
      <b-form-checkbox v-model="lineFrom" switch>
        {{ $t('addDictionary.firstLineIsHead') }}
      </b-form-checkbox>
    </div>

    <b-row class="row-parsed-options" v-for="(field, i) in fields" :key="i">
      <b-col cols="3">
        <label :for="`${field}-select`">
          {{ $t(`addDictionary.field.${field}`) }}
        </label>
      </b-col>
      <b-col>
        <b-row>
          <b-col>
            <!-- from example line -->
            <b-form-select
              :state="validation(field)"
              :id="`${field}-select`"
              v-model="match[field]"
              ref="select"
            >
              <b-form-select-option :value="null">
                -- {{ $t('addDictionary.field.ignore') }} --
              </b-form-select-option>
              <b-form-select-option
                v-for="(text, col) in example"
                :value="col"
                :key="col"
              >
                {{ text }}
              </b-form-select-option>
            </b-form-select>
          </b-col>
          <!-- extra choose -->
          <template v-if="field === 'id' || field === 'lang' || field === 'native'">
            <b-col cols="1">{{ $t('addDictionary.field.or') }}</b-col>
            <b-col>
              <b-form-checkbox v-if="field === 'id'" checked disabled switch>
                {{ $t('addDictionary.idGenered') }}
              </b-form-checkbox>

              <b-form-select v-else :class="`langs-select ${field}-select`" v-model="this[field]">
                <b-form-select-option
                  v-once
                  v-for="lang in Object.keys(langs)"
                  :value="lang"
                  :key="lang"
                >
                  {{ $t(`langs.${lang}`) }}
                </b-form-select-option>
              </b-form-select>
            </b-col>
          </template>
        </b-row>
      </b-col>
    </b-row>
  </b-form>
</template>

<script>
import { verifyItem, normalizeItem } from '@/utils/item.js';

export default {
  name: 'DitParseCsv',
  emits: ['parse:error'],
  props: {
    content: String,
    type: String,
  },
  inject: ['fields', 'valid', 'native', 'collection', 'langs'],
  data() {
    const langIds = Object.keys(this.langs);
    const langNames = new Map(
      Object.values(this.langs).map((name, i) => [name.toLowerCase(), langIds[i]])
    );

    return {
      lineFrom: false,
      lang: null,
      langNames,
      match: {},
      parsed: [],
    };
  },
  computed: {
    example() {
      return this.parsed[this.lineFrom ? 1 : 0] || [];
    },
  },
  watch: {
    content(content) {
      this.parsed = this.parse(content);
    },
    example(line) {
      this.fields.forEach((field, i) => {
        this.match[field] = line[i] ? i : null;
      });
    },
  },
  created() {
    this.fields.forEach((field, i) => {
      this.match[field] = i;
      this.$watch(`match.${field}`, (newCol, oldCol) => { this.onChangeIndex(field, newCol, oldCol) });
    });
  },
  methods: {
    onChangeIndex(changedField, newCol, oldCol) {
      if (newCol === null) return;
      const needChangeField = this.fields.find((field) => {
        return field !== changedField && this.match[field] === newCol;
      });
      this.match[needChangeField] = oldCol;
    },
    parse(content) {
      const lines = [];
      try {
        content
          .trim()
          .split(/[\n\r]+/).forEach((line) => {
            lines.push(
              line
                .split(/(?:^|,)(?:"((?:[^"]|"")*)"|([^",]*))/)
                .filter((text) => text)
            );
          });
      } catch (e) {
        this.$emit('parse:error', {
          type: 'parse',
          options: { type: this.type },
        });
      }
      return lines;
    },
    validation(field) {
      let isValid = false;
      const col = this.match[field];
      const value = this.example[col];
      switch (field) {
        case 'translation':
        case 'text':
          isValid = !!value;
          break;
        case 'lang':
        case 'native':
          if (value) {
            const [from, to] = value.toLowerCase().split('-');
            let lang = this.langs[from] ? from : this.langs[to] && to;
            if (!lang) {
              lang = this.langNames.get(value);
            }

            isValid = !!lang;
            if (isValid) this[field] = lang;
            break;
          }

          isValid = !!this.langs[this[field]];
          break;
        case 'id':
          if (value) {
            isValid = new Map(
              this.parsed.map((line) => [line[col]])
            ).size === this.parsed.length;
            break;
          }
        // fall through
        default:
          isValid = true;
      }
      this.valid[field] = isValid;
      return isValid;
    },
    getItems() {
      const items = [];
      for (
        let i = this.lineFrom ? 1 : 0, n = this.parsed.length, id = Date.now();
        i < n;
        i++, id++
      ) {
        const line = this.parsed[i];
        const item = { collectionID: this.collection.id };
        this.fields.forEach((field) => {
          const col = this.match[field];
          let value = line[col];
          switch (field) {
            case 'native':
            case 'lang':
              value = value && (
                // lang id or name
                this.langs[value] ? value : this.langNames.get(value.toLowerCase())
                ) || this[field];
              break;
            case 'id':
              value |= id;
          }
          item[field] = value;
        });
        const verifiedItem = verifyItem(normalizeItem(item));
        verifiedItem && items.push(verifiedItem);
      }

      return items;
    },
  },
};
</script>
