<template>
  <b-form v-if="level" class="settings-complexity" @change="onChange">
    <iconify-icon
      class="settings-complexity-icon"
      :title="$t('settings.training.complexity')"
      icon="carbon:skill-level-advanced"
    ></iconify-icon>
    <input
      name="complexity"
      class="settings-complexity-input"
      type="range"
      orient="vertical"
      :min="level.min"
      :max="level.max"
      :step="level.step"
      v-model.number="value"
      list="complexityMarks"
      ref="input"
    />
    <datalist id="complexityMarks">
      <option
        v-for="mark in Math.round(level.max / level.step)"
        :key="mark"
        :value="Math.round(mark * level.step * 10) / 10"
      ></option>
    </datalist>
  </b-form>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'Setting for training',
  components: {
    IconifyIcon,
  },
  data() {
    const { trainingType, complexity, settings } = this.$store.getters;
    return {
      value:
        settings.training.complexity[trainingType] ||
        complexity[trainingType]?.default,   // card
    };
  },
  computed: {
    type() {
      return this.$store.getters.trainingType;
    },
    complexitySettings() {
      return this.$store.getters.settings.training.complexity;
    },
    complexitySettingsValue() {
      return this.$store.getters.settings.training.complexity[this.type];
    },
    level() {
      return this.$store.getters.complexity[this.type];
    },
  },
  watch: {
    type(type) {
      this.value = this.complexitySettings[type] || this.level?.default;
    },
    complexitySettingsValue(value) {
      // rebound by the count of training items
      this.value = value;
    },
  },
  updated() {
    if (!this.level) return;
    this.update();
  },
  methods: {
    update() {
      this.$refs.input.value = this.value;
    },
    onChange() {
      this.complexitySettings[this.type] = parseFloat(this.value);
    },
  },
};
</script>
