<template>
  <b-form-group
    :class="`form-floating
      complexity-${complexity?.toString().replace('.', '-')}`">
    <b-form-textarea
      id="textarea"
      class="writing-input"
      size="lg"
      autocomplete="off"
      autocorrect="off"
      autofocus

      :state="isValid"
      @blur="valid"
      :onkeyup="check"
      v-model="examText"
    ></b-form-textarea>
    <div class="writing-shadow writing-input form-control form-control-lg">
      <span
        :class="`writing-shadow-letter letter-${i}`"
        v-for="(letter, i) in text"
        :key="i"
        >{{ letter }}</span
      >
    </div>
  </b-form-group>
</template>

<script>
export default {
  name: 'TrianingByWriting',
  inject: ['buttons'],
  props: { item: Object },
  data() {
    return {
      isSuccess: null,
      isScored: null,
      isValid: null,
      examText: '',
    };
  },
  computed: {
    text() {
      return this.item.text.toLowerCase();
    },
    complexity() {
      return this.$store.getters.settings?.training.complexity.writing;
    },
  },
  watch: {
    item() {
      this.clear();
    },
    'buttons.right.pressed'() {
      this.exam();
    },
  },
  mounted() {
    this.clear();
    window.addEventListener('keydown', this.setFocus);
  },
  unmounted() {
    window.removeEventListener('keydown', this.setFocus);
    this.$store.commit('loaded', { currentTraining: undefined });
  },
  methods: {
    setFocus() {
      document.getElementById('textarea').focus();
    },
    clear() {
      this.$store.commit('loaded', { currentTraining: false });
      this.examText = '';
      this.isSuccess = null;
      this.isScored = null;
      this.isValid = null;
      this.$store.commit('loaded', { currentTraining: true });
      this.onSuccess();
      this.setFocus();
    },
    check() {
      this.isSuccess = this.examText
        ? this.examText.toLowerCase() === this.text
        : null;
      this.onSuccess();
      this.valid();
    },
    valid(e) {
      // e exists for blur
      this.isValid = e ? this.isSuccess : this.isSuccess || null;
    },
    onSuccess() {
      this.$store.commit('success', this.isSuccess);
      if (!this.isSuccess) return;

      this.$speak(this.item);
      this.exam();
    },
    exam() {
      if (this.isScored) return;
      this.item.score.exam({
        name: 'writing',
        result: this.isSuccess ? 'success' : 'skip',
      });
      this.isScored = true;
    },
  },
};
</script>
