<template>
  <b-button
    v-if="text && speakSettings.enabled && speakSettings[lang]"
    @click="$speak($props)"
    variant="outline-secondary"
    class="speech-button"
    pill
  >
    <iconify-icon icon="bi:volume-down"></iconify-icon>
  </b-button>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'Speech',
  components: {
    IconifyIcon,
  },
  props: {
    text: String,
    lang: String,
    autospeak: [Boolean],
  },
  computed: {
    speakSettings() {
      return this.$store.getters.settings?.speak || {};
    },
  },
  mounted() {
    this.autoSpeak();
  },
  updated() {
    this.autoSpeak();
  },
  methods: {
    autoSpeak() {
      if (!this.speakSettings.autoplay) return;
      if (!this.autospeak) return;
      this.$speak(this.$props);
    },
  },
};

// For unvisual use
function Stop() {
  speechSynthesis.cancel();
}
window.addEventListener('dblclick', Stop);
window.onbeforeunload = () => {
  window.removeEventListener('dblclick', Stop);
};

function Speak({ text, lang }) {
  Stop();
  if (!text) return;

  const speakSettings = this.$store.getters.settings?.speak;
  if (!speakSettings.enabled) return;
  if (!speakSettings.readyState) return;

  const utterThis = new SpeechSynthesisUtterance();

  lang = lang || this.lang || this.$store.getters.settings.training.lang;
  utterThis.lang = lang;

  const speakLangSettings = speakSettings[lang];
  if (!speakLangSettings) return;

  const voiceURI = speakLangSettings.voiceURI;
  if (voiceURI && speakLangSettings.voice?.voiceURI !== voiceURI) {
    this.$store.getters.voices[lang].some((voice) => {
      if (voice.voiceURI === voiceURI) {
        speakLangSettings.voice = voice;
        return true;
      }
    });
  }

  Object.keys(speakLangSettings).forEach((option) => {
    if (option in utterThis) utterThis[option] = speakLangSettings[option];
  });

  utterThis.text = text;
  speechSynthesis.speak(utterThis);
}

export const speechPlugin = {
  async install(app) {
    app.mixin({
      beforeCreate() {
        this.$speak = (...args) => Speak.call(this, ...args);
      },
      unmounted() {
        delete this.$speak;
      },
    });
  },
};
</script>
