<template>
  <b-offcanvas
    v-model="showDictionary"
    v-show="showDictionary"
    :title="title"
    id="dictionary-offcanvas"
    ref="modal"
  >
    <dictionary from="offcanvas"></dictionary>
  </b-offcanvas>
  <b-row class="main">
    <b-col cols="1">
      <b-button
        class="dictionary-button"
        :title="title"
        variant="outline"
        @mouseover="onToggleDictionary"
      >
        <h5 class="text-secondary">
          <iconify-icon icon="mdi:arrow-collapse-right"></iconify-icon>
        </h5>
      </b-button>
    </b-col>
    <b-col cols="10">
      <router-view></router-view>
    </b-col>
  </b-row>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
import Dictionary from '@/views/Dictionary/Dictionary';

export default {
  name: 'Dictionary Layout',
  components: {
    IconifyIcon,
    Dictionary,
  },
  data() {
    return {
      showDictionary: false,
    };
  },
  computed: {
    url() {
      return this.$route.href;
    },
    title() {
      return this.$t('dictionary.title', { name: this.$store.getters.collection?.name });
    },
  },
  watch: {
    url() {
      this.showDictionary = false;
    },
  },
  methods: {
    onToggleDictionary() {
      this.showDictionary = !this.showDictionary;
      if (!this.showDictionary) return;

      window.setTimeout(() => {
        this.$refs.modal.$refs.element.focus();
      }, 300);
    },
  },
};
</script>
