<template>
  <div class="training-card" @click="onClick">
    <!-- todo: front  synonims from article / db -->
    <div class="training-card-front">{{ item.translation }}</div>
    <div class="training-card-back">
      <span class="training-card-back-text">{{ item.text }}</span>
    </div>
    <div class="training-card-size invisible">{{ item.text }}</div>
  </div>
</template>

<script>
export default {
  name: 'TrainingByCard',
  inject: ['buttons'],
  props: { item: Object },
  data() {
    return {
      isShown: false,
      isScored: false,
    };
  },
  watch: {
    'buttons.left.pressed'() {
      this.isShown && this.exam('error');
    },
    'buttons.right.pressed'() {
      if (!this.isShown) return this.onClick();
      this.exam('success');
      this.$store.commit('success', true);
    },
    item() {
      this.clear();
    },
  },
  beforeUpdate() {
    this.clear();
  },
  mounted() {
    this.clear();
  },
  unmounted() {
    this.$store.commit('loaded', { currentTraining: undefined });
    this.buttons?.set({ right: { specialType: undefined } });
  },
  methods: {
    async clear() {
      this.$store.commit('loaded', { currentTraining: false });
      await this.$store.commit('success', false);
      this.$el.classList?.remove('shown');
      this.isShown = false;
      this.isScored = false;
      this.setButtons();
      this.$store.commit('loaded', { currentTraining: true });
    },
    onClick() {
      this.isShown = !this.isShown;

      this.setButtons();
      this.$el.classList.toggle('shown', this.isShown);

      this.isShown && this.$speak(this.item);
    },
    setButtons() {
      if (!this.item) return;
      if (!this.buttons) return;
      this.buttons.set(
        this.isShown
          ? {
              right: { type: 'correct' },
              left: { type: 'incorrect' },
            }
          : {
              right: { specialType: 'check', type: 'check' },
              left: { type: 'results' },
            }
      );
    },
    exam(result) {
      if (this.isScored) return;
      this.item.score.exam({
        name: 'card',
        result,
      });
      this.isScored = true;
    },
  },
};
</script>
