<template>
  <!-- Alert -->
  <dit-alert v-if="alert"></dit-alert>
  <!-- Dictionary -->
  <div v-show="!alert" class="dictionary">
    <dict-pager
      :total-rows="filteredItems.length"
      :per-page="perPage"
      v-model="currentPage"
    />

    <b-table
      id="dictionary"
      hover
      primary-key="id"
      :fields="fields"
      :items="pageItems"
    >

      <!-- Text -->
      <template v-if="langs.length > 1" #head(text)="data">
        <b-form-select id="filtered-lang" v-model="filtered.lang" class="lang-icon">
          <b-form-select-option :value="null">
            {{ $t('dictionary.lang') }}
          </b-form-select-option>
          <b-form-select-option v-for="lang in langs" :value="lang" :key="lang">
            {{ lang.toUpperCase() }}
          </b-form-select-option>
        </b-form-select>
        {{ data.label }}
      </template>
      <template #cell(text)="data">
        <dit-lang-icon :show="!filtered.lang" :lang="data.item.lang"></dit-lang-icon>
        <speech
          size="sm"
          :text="data.item.text"
          :lang="data.item.lang"
        ></speech>
        {{ data.item.text }}
      </template>

      <!-- Translation -->
      <template v-if="natives.length > 1" #head(translation)="data">
        <b-form-select id="filtered-natives" v-model="filtered.native" class="lang-icon">
          <b-form-select-option :value="null">
            {{ $t('dictionary.native') }}
          </b-form-select-option>
          <b-form-select-option v-for="lang in natives" :value="lang" :key="lang">
            {{ lang.toUpperCase() }}
          </b-form-select-option>
        </b-form-select>
        {{ data.label }}
      </template>
      <template v-if="natives.length > 1" #cell(translation)="data">
        <dit-lang-icon :show="!filtered.native" :native="data.item.native"></dit-lang-icon>
        {{ data.item.translation }}
      </template>

      <!-- Article -->
      <template #head(article)></template>
      <template #cell(article)="data">
        <training-actions
          :item="data.item"
          :actions="['article']"
        ></training-actions>
      </template>

      <!-- Score -->
      <template #head(score)>{{ $t('dictionary.score') }}</template>
      <template #cell(score)="data">
        <training-actions
          :item="data.item"
          :actions="['score']"
        ></training-actions>
      </template>
    </b-table>

    <dict-pager
      :total-rows="filteredItems.length"
      :per-page="perPage"
      v-model="currentPage"
    />
  </div>
</template>

<script>
import Speech from '@/components/Speech/Speech';
import TrainingActions from '@/components/Training/Actions';
import DitAlert from '@/components/DitAlert/DitAlert';
import DictPager from '@/components/Dictionary/Pager';
import DitLangIcon from '@/components/DitLangIcon/DitLangIcon';

export default {
  name: 'Dictionary',
  props: {
    from: [String, Boolean],
  },
  components: {
    DitAlert,
    DictPager,
    DitLangIcon,
    Speech,
    TrainingActions,
  },
  data() {
    return {
      filtered: {
        native: null,
        lang: null,
        items: null,
      },
      currentPage: 1,
      perPage: global.isTouchDevice ? 15 : 20,
    };
  },
  computed: {
    alert() {
      return !this.from && this.$store.getters.alert;
    },
    items() {
      return this.$store.getters.items || [];
    },
    natives() {
      return this.$store.getters.natives;
    },
    langs() {
      return this.$store.getters.langs;
    },
    filteredItems() {
      return this.filtered.items || this.items;
    },
    pageItems() {
      const startIndex = (this.currentPage - 1) * this.perPage;
      return this.filteredItems.slice(startIndex, startIndex + this.perPage);
    },
    fields() {
      return ['text', 'translation', 'article', 'score']
        .filter((key) => {
          if (key === 'native' && this.natives.length < 2) return;
          if (key === 'lang' && this.langs.length < 2) return;
          return key;
        })
        .map((key) => {
          return {
            key,
            label: this.$t('dictionary.' + key.toLowerCase()),
          };
        });
    },
  },
  watch: {
    '$store.getters.collection'() {
       !this.from && this.setTitle() },
    'filtered.native'() { this.doFiltered() },
    'filtered.lang'() { this.doFiltered() },
  },
  created() {
    this.setTitle();
  },
  methods: {
    doFiltered() {
      const { native, lang } = this.filtered;
      this.filtered.items = this.items.filter((item) => {
        return (!native || (native === item.native)) && (!lang || (lang == item.lang));
      });
      this.currentPage = 1;
    },
    setTitle() {
      this.$store.commit('title', () => this.$t('dictionary.title', { name: this.$store.getters.collection?.name }));
    }
  },
};
</script>
