<template>
  <b-alert :modelValue="!!alert" variant="warning">
    <iconify-icon v-if="alert.icon" :icon="alert.icon" />
    <h5 class="alert-title">
      {{ $t(`alert.${alert.name}.title`, alert.data) }}
    </h5>
    {{ $t(`alert.${alert.name}.text`, alert.data) }}
  </b-alert>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';

export default {
  name: 'DitAlert',
  props: { params: Object },
  components: {
    IconifyIcon,
  },
  computed: {
    alert() {
      const alert = this.$store.getters.alert;
      alert && Object.assign(alert.data, this.params);
      return alert;
    },
  },
};
</script>
