import { createI18n } from 'vue-i18n/index';

const fallbackLocale = {
  default: ['en', 'ru'],
  kk: 'ru',
};
const localeFiles = require.context('./locales', true, /[A-Za-z0-9-_,\s]+\.json$/i);
const { messages, locale } = loadLocaleMessages();

function loadLocaleMessages() {
  let locale;
  const messages = {};
  const currentLocales = [
    ...new Set([
      ...navigator.languages,
      ...navigator.languages.map((lang) => lang.split('-')[0])
    ])
  ];

  localeFiles.keys().forEach((key) => {
    const matched = key.match(/([A-Za-z0-9-_]+)\./i);
    if (matched && matched.length > 1) {
      const lang = matched[1];
      messages[lang] = null;
    }
  });
  locale = currentLocales.filter((lang) => {
    if (Object.prototype.hasOwnProperty.call(messages, lang)) return true;
  })[0];
  return { messages, locale };
}

function ruPluralization(choice, choicesLength) {
  if (choice === 0) {
    return 0;
  }

  const teen = choice > 10 && choice < 20;
  const endsWithOne = choice % 10 === 1;

  // fallback en+ rules (1, 21, 31, 41, 1151 or others)
  if (choicesLength === 2) {
    return !teen && endsWithOne === 1 ? 0 : 1;
  }

  // one 1, 21, 31, 41, 1151
  if (!teen && endsWithOne) {
    return 1;
  }
  // some 2, 3, 4, 22, 34, 1253
  if (!teen && choice % 10 >= 2 && choice % 10 <= 4) {
    return 2;
  }
  // many 5, 6, 7, 11, 12, 13, 14, 105, 5214
  return choicesLength < 4 ? 2 : 3;
}

function missingHandler(locale, key) {
  window.postMessage({ type: 'i18n:missing', locale, key });
}

const I18n = createI18n({
  // todo: Get default locale from request headers
  locale,
  messages,
  // Locales that don't exist will fallback to English
  fallbackLocale,
  // Falling back to fallbackLocale generates two console warnings
  // Silent fallback suppresses console warnings when using fallback
  silentFallbackWarn: false,
  allowComposition: true,
  // legacy: false,
  pluralizationRules: {
    ru: ruPluralization,
  },
  missing: missingHandler,
  warnHtmlInMessage: 'off',
});
export default I18n; // info: for main.js and vue components

const i18n = I18n.global;
export { i18n };    // info: for js ounside of components
