<template>
  <template v-if="callback">
    <router-view></router-view>
  </template>
  <template v-else>
    <!-- Spinner -->
    <div v-show="!show && [header, view, footer].indexOf('div') !== -1" class="spinner-container">
      <b-spinner class="loader" variant="warning"></b-spinner>
    </div>

    <component :is="header" :settings="settings" />
    <component :is="view" />
    <component :is="footer" />
  </template>
</template>

<script>
import { loadIcons } from '@iconify/vue';
import allIcons from './utils/iconify';
import AppHeader from '@/components/App/Header.vue';
import AppFooter from '@/components/App/Footer.vue';

export default {
  name: 'App',
  data() {
    return {
      header: 'div',
      view: 'div',
      footer: 'div',
    };
  },
  components: {
    AppHeader,
    AppFooter,
  },
  computed: {
    settings() {
      return this.$store.getters.settings;
    },
    callback() {
      return this.$route.meta.active === 'callback';
    },
    show() {
      return this.settings && this.$store.getters.i18nReady;
    },
  },
  watch: {
    show() {
      this.setView();
    },
    async 'settings.ui.locale'() {
      this.$i18n.locale = this.settings.ui.locale;
      await this.$store.commit('localeChanged');
    },
    '$store.getters.i18nUpdated'() {
      this.$store.commit('title');
    },
  },
  async beforeCreate() {
    if (this.callback) return;

    const onMessage = ({ data }) => {
      switch (data.type) {
        case 'i18n:missing':
          this.$store.dispatch('fetchI18n', data);
          break;
      }
    };
    onMessage.bind(this);
    window.addEventListener('message', onMessage);

    await this.$store.dispatch('fetchSettings');
    await this.$store.dispatch('fetchI18n');
    await this.$store.dispatch('fetchCollections');
  },
  created() {
    loadIcons(allIcons);

    if (global.isTest) {
      document.body.classList.add('notransition');
    }

    speechSynthesis.onvoiceschanged = () => {
      // exist both: langs and speechSynthesis
      this.$store.commit('setVoices');
    };
  },
  methods: {
    setView() {
      if (!this.show) return;
      this.header = 'app-header';
      this.view = 'router-view';
      this.footer = 'app-footer';
    },
  },
};
</script>
