<template>
  <table v-if="shownResult" class="training-results">
    <tr v-for="item in bulkItems" :key="item.id">
      <td>
        <div class="training-results-text">
          <dit-lang-icon :lang="item.lang" />
          {{ item.text }}
        </div>
      </td>
      <td>
        {{ item.translation }}
      </td>
      <td>{{ item.score.value }}</td>
    </tr>
  </table>
  <div class="training-desk">
    <keep-alive>
      <training v-if="!shownResult"></training>
    </keep-alive>
  </div>
</template>

<script>
import DitLangIcon from '@/components/DitLangIcon/DitLangIcon';
import Training from '@/views/Training/Training';

export default {
  name: 'Bulk Training',
  inject: ['buttons'],
  components: {
    DitLangIcon,
    Training,
  },
  data() {
    return {
      shownResult: false,
    };
  },
  computed: {
    isSuccess() {
      return this.$store.getters.isSuccess;
    },
    bulkItems() {
      return this.$store.getters.bulkItems;
    },
    item() {
      return this.$store.getters.trainingItem;
    },
  },
  watch: {
    '$store.getters.settings.training.count'() {
      this.update();
    },
    '$store.getters.settings.training.textType'() {
      this.update();
    },
    '$store.getters.settings.training.lang'() {
      this.update();
    },
    '$store.getters.settings.training.native'() {
      this.update();
    },
    '$store.getters.settings.training.useReverseTranslation'() {
      this.update();
    },
    'buttons.left.pressed'() {
      this.onPressButton('left');
    },
    'buttons.right.pressed'() {
      this.onPressButton('right');
    },
    '$store.getters.trainingType'() {
      this.setButtons();
      this.shownResult = false;
    },
    '$store.getters.itemsLength'() {
      this.update();
    },
    isSuccess() {
      this.setButtons();
    },
    item(item) {
      if (!item) return;

      this.setButtons();
      this.shownResult = false;
    },
    shownResult() {
      this.setButtons();
    },
  },
  beforeCreate() {
    this.$store.commit('loaded', { bulk: false, training: false });
  },
  async created() {
    this.update();
  },
  unmounted() {
    this.$store.commit('alert', { noneItems: undefined });
    this.$store.commit('loaded', { bulk: undefined, training: undefined });
  },
  methods: {
    next() {
      if (this.item) {
        if (this.shownResult) {
          this.shownResult = false;
          return;
        }

        const successType = this.buttons.right.type;
        if (
          this.isSuccess ||
          successType === 'skip' ||
          successType === 'correct' ||
          successType === 'incorrect' ||
          successType === 'continue'
        ) {
          this.$store.commit('nextItem');
          if (!this.item) this.shownResult = true;
        }
      } else this.update();
    },
    update() {
      if (!this.$store.getters.itemsLength) return; // before dictionary loaded

      this.$store.commit('loaded', { bulk: false });

      this.$store.commit('setBulkOptions');

      this.$store.commit('loaded', { bulk: true });
    },
    setButtons() {
      this.buttons.set(
        this.shownResult
          ? {
              // note: it's a hack for score.exam
              left: {
                type: this.item ? 'continue' : 'restart',
              },
              right: {
                visible: false,
              },
            }
          : {
              left: {
                type: 'results',
              },
              right: {
                type: this.isSuccess
                  ? this.buttons.right.specialType || 'continue'
                  : 'skip',
                visible: true,
              },
            }
      );
    },
    onPressButton(name) {
      switch (this.buttons[name].type) {
        case 'check':
          return;
        case 'results':
          this.shownResult = true;
          return;
        case 'continue':
          if (this.shownResult) {
            this.shownResult = false;
            return;
          }
        // case 'skip':
        // case 'restart':
        // case 'correct':
        // case 'incorrect':
      }
      return this.next();
    },
  },
};
</script>
