<template>
  <div class="yandex-auth">
    <div v-if="!isAuthenticated">
      <b-button @click="login" :title="`${$t('auth.login')} ${$t('auth.yandex')}`" class="yandex-auth-btn login" variant="none">
        <b-img src="/static/images/yandex.svg" />
      </b-button>
    </div>
    <div v-else>
      <b-button @click="logout" :title="$t('auth.logout')" class="yandex-auth-btn logout" variant="none ">
        <b-img class="yandex-logo" rounded="circle" :src="`${$store.getters.yandex.avatar}/islands-middle`" />
        {{ $store.getters.yandex.email }}
      </b-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'YandexAuth',
  computed: {
    isAuthenticated() {
      return this.$store.getters.yandex.isAuthenticated;
    },
  },
  methods: {
    login() {
      const {
        VUE_APP_YANDEX_AUTH_URL,
        VUE_APP_YANDEX_CLIENT_ID,
        VUE_APP_YANDEX_CALLBACK_URL,
      } = process.env;

      window.open(
        `${VUE_APP_YANDEX_AUTH_URL}/authorize?` +
          `response_type=code` +
          `&client_id=${VUE_APP_YANDEX_CLIENT_ID}` +
          `&redirect_uri=${window.location.origin}${VUE_APP_YANDEX_CALLBACK_URL}`,
        'yandexAuth',
        'popup,left=0,top=0,width=630,height=640'
      );
    },
    logout() {
      this.$store.commit('logout', 'yandex');
    }
  },

  async created() {
    this.$store.dispatch('checkAuth', 'yandex');

    this.$watch(
      () => this.$route.params,
      async () => {
        if (this.$store.getters.yandex.isAuthenticated) return;

        const code = window.location.hash.split('#yandex-code=')[1];
        if (!code) return;

        window.location.hash = '';
        this.$store.dispatch('auth', { yandex: code });
      }
    );
  },

};
</script>
