<template>
  <div class="dit-add-dictionary">
    <template v-if="show">
      <h5>
        <label for="input-file" class="form-label">
          {{ $t('addDictionary.input') }}
        </label>
      </h5>
      <input
        class="form-control"
        type="file"
        id="input-file"
        ref="doc"
        @change="readFile()"
      />

      <!-- Info -->
      <div class="upload">
        <div v-if="error" class="error upload-text">
          <iconify-icon icon="mdi:error-outline" />
          {{ error }}
        </div>
        <div
          v-if="(!content && !error) || (error && type === 'json')"
          class="upload-about upload-about-json"
        >
          <i18n-t
            v-once
            keypath="addDictionary.jsonFormat.text"
            class="upload-text"
            tag="div"
          >
            <template #list>
              <i18n-t
                :keypath="`addDictionary.jsonFormat.list[${i}]`"
                tag="li"
                v-for="(path, i) in $tm('addDictionary.jsonFormat.list')"
                :key="i"
              >
                <template #yandex>
                  <b>{{ $t('addDictionary.jsonFormat.yandex') }}</b>
                </template>
                <template v-for="field in fields" :key="field" #[field]>
                  <i>{{ field }}</i>
                </template>
              </i18n-t>
            </template>
          </i18n-t>
        </div>
        <div
          v-if="(!content && !error) || (error && type === 'csv')"
          class="upload-about upload-about-csv"
        >
          <i18n-t
            v-once
            class="upload-text"
            tag="div"
            keypath="addDictionary.csvFormat.text"
          >
            <template #google>
              <b>{{ $t('addDictionary.csvFormat.google') }}</b>
            </template>
            <template #path>
              <template
                v-for="(path, i) in $tm('addDictionary.csvFormat.path')"
                :key="i"
              >
                {{ i ? '  ' : '' }}<i>{{ path }}</i>
              </template>
            </template>
          </i18n-t>
        </div>
      </div>

      <!-- Parse file content -->
      <div v-show="!error && content && type">
        <hr />
        <component
          :is="`dit-parse-${type}`"
          :content="content"
          :type="type"
          @parse:error="onError"
          ref="parser"
        ></component>
        <hr />
        <h5>{{ $t('addDictionary.dictionaryName') }}</h5>
        <b-form class="add-dictionary-form" @submit="save">
          <b-input-group label-cols="auto">
            <b-form-input
              :state="nameValid"
              v-model="collection.name"
            ></b-form-input>
            <b-button class="btn-save" :disabled="!formValid" variant="primary" @click="save">{{ $t('addDictionary.save') }}</b-button>
            <b-form-invalid-feedback v-if="collection.name && !nameValid">{{ $t('addDictionary.error.collectionExists') }}</b-form-invalid-feedback>
          </b-input-group>
        </b-form>
      </div>
    </template>
    <template v-once v-else>
      <i18n-t tag="p" keypath="addDictionary.dictionaryAdded" class="add-dictionary-success">
        <template #link>
          <b-link @click="show = true">{{ $t('addDictionary.add') }}</b-link>
        </template>
        <template #name>{{ collection.name }}</template>
      </i18n-t>
    </template>
  </div>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
import DitParseCsv from '@/components/DitAddDictionary/DitParseCsv.vue';
import DitParseJson from '@/components/DitAddDictionary/DitParseJson.vue';

export default {
  name: 'Info',
  components: {
    IconifyIcon,
    DitParseCsv,
    DitParseJson,
  },
  data() {
    const native = this.$store.getters.settings.training.native;

    return {
      error: null,
      file: null,
      type: undefined,
      content: '',
      native,
      langs: this.$tm('langs'),
      valid: {},
      collection: {
        id: null,
        name: null,
      },
      fields: [
        'text',
        'translation',
        'transcription',
        'lang',
        'native',
        'id',
      ],
      show: true,
    };
  },
  provide() {
    return {
      fields: this.fields,
      valid: this.valid,
      langs: this.langs,
      native: this.native,
      collection: this.collection,
    };
  },
  computed: {
    formValid() {
      return this.nameValid && !Object.values(this.valid).includes(false);
    },
    nameValid() {
      return !!this.collection.name && !this.$store.getters.collections.find(
        (collection) => collection.name === this.collection.name
      );
    },
  },
  created() {
    this.fields.forEach((field) => {
      this.valid[field] = false;
    });
    this.$store.commit('title', () => this.$t('addDictionary.title'));
  },
  methods: {
    clear() {
      this.type = undefined;
      this.error = null;
      this.content = '';
    },
    readFile() {
      this.clear();

      this.file = this.$refs.doc.files[0];
      if (!this.file) return;

      this.type = this.file.type.split('/')?.[1];
      switch (this.type) {
        case 'csv':
        case 'json':
          break;
        default:
          return this.onError({ type: 'unknown' });
      }

      const reader = new FileReader();
      reader.onload = (res) => {
        this.error = null;
        this.content = res.target.result;
        this.collection.id = String(Math.random()).split('.')[1];
        this.collection.name = this.file.name.replace(/\.[^.]*$/, '');
      };
      reader.onerror = () => { this.onError({ type: 'read' }) };
      reader.readAsText(this.file);
    },
    async save() {
      const items = this.$refs.parser.getItems();
      this.$store.dispatch('syncCollectionsDB', this.collection);
      this.$store.dispatch('syncDictionaryDB', items);

      // update
      await this.$store.dispatch('fetchCollections', { force: true });
      if (this.$store.getters.collections.find((collection) => collection.id === this.collection.id)) {
        this.show = false;
        this.$refs.doc.value = null;
        this.clear();
      } else {
        this.onError({ type: 'collectionAdd' });
      }
    },
    onError({ type, options }) {
      this.error = this.$t(`addDictionary.error.${type}`, options);
    },
  },
};
</script>
