<template>
  <b-navbar class="header">
    <div class="header-logo-group">
      <b-navbar-brand to="/">
        <picture :title="$t('global.title')">
          <source srcset="/static/images/logo.svg" media="(max-width: 630px)" height="35" />
          <img src="/static/images/logo__full.png" />
        </picture>
      </b-navbar-brand>

      <div v-if="settings" class="per-day-summary digital-summary">
        <digital-count
          :count="summary"
          :lengthBy="settings.training.goalPerDay"
        ></digital-count
        ><iconify-icon icon="fad:digital-colon"></iconify-icon
        ><digital-count :count="settings.training.goalPerDay"></digital-count>
      </div>
    </div>

    <b-navbar-nav>
      <!-- Training -->
      <b-nav-item :active="$route.meta.active === 'training'" to="/training">
        {{ $t('training.name') }}

        <div
          class="training-summary digital-summary"
          v-show="
            $route.meta.active === 'training' &&
            countPos &&
            currentPos <= countPos
          "
        >
          <digital-count
            :count="currentPos"
            :lengthBy="countPos"
          ></digital-count
          ><iconify-icon icon="fad:digital-colon"></iconify-icon
          ><digital-count :count="countPos"></digital-count>
        </div>
      </b-nav-item>

      <!-- Dictionary -->
      <b-nav-item :active="$route.meta.active === 'dictionary'" to="/dictionary">
        {{ $t('dictionary.name') }}
        <digital-count :count="dictionaryLength"></digital-count>
      </b-nav-item>

      <!-- Settings -->
      <b-button
        v-if="settings"
        :title="$t('settings.name')"
        variant="outline"
        @click="onToggleSettings"
        class="actions-btn-settings"
        aria-controls="modal-settings"
      >
        <iconify-icon icon="material-symbols:settings-alert-outline"></iconify-icon>
        <b-offcanvas
          placement="end"
          v-model="showSettings"
          :title="$t('settings.name')"
          id="modal-settings"
          ref="modal"
        >
          <app-settings></app-settings>
        </b-offcanvas>
      </b-button>
    </b-navbar-nav>
  </b-navbar>
</template>

<script>
import { Icon as IconifyIcon } from '@iconify/vue';
import AppSettings from '@/views/Settings/Settings';
import DigitalCount from '@/components/DigitalCount';

export default {
  name: 'App',
  components: {
    IconifyIcon,
    AppSettings,
    DigitalCount,
  },
  props: { settings: Object },
  data() {
    this.$store.commit('summaryInit');
    return {
      showSettings: false,
    };
  },
  computed: {
    dictionaryLength() {
      return this.$store.getters.itemsLength;
    },
    summary() {
      return this.$store.getters.summary;
    },
    currentPos() {
      return this.$store.getters.indexTrainingItem + 1;
    },
    countPos() {
      return this.$store.getters.bulkLength;
    },
  },
  created() {
    this.$watch(
      () => this.$route.path,
      async () => {
        this.showSettings = false;
      }
    );
  },
  methods: {
    onToggleSettings() {
      this.showSettings = !this.showSettings;
      if (!this.showSettings) return;

      window.setTimeout(() => {
        this.$refs.modal.$refs.element.focus();
      }, 300);
    },
  },
};
</script>
