<template>
  <div class="settings">
    <b-accordion flush>
      <!-- Trainings -->
      <b-accordion-item class="settings-section settings-trainings">
        <template #title>
          <div class="col-form-label-lg settings-section-title">
            {{ $t('settings.training.title') }}
          </div>
        </template>
        <b-form class="settings-training" @change="onTrainingsSettingsChange">
          <b-form-group>
            <!-- Collections -->
            <b-form-group
              label-cols="auto"
              label-for="collectons-options"
              :label="$t('settings.collections')"
            >
              <b-form-select
                id="collections-options"
                v-model="settings.collectionID"
              >
                <b-form-select-option
                  v-for="collection in collections"
                  :value="collection.id"
                  :key="collection.id"
                >
                {{ collection.name }}
                </b-form-select-option>
              </b-form-select>
              <b-link class="hint" to="/upload">{{
                $t('settings.addDictionary')
              }}</b-link>
            </b-form-group>
            <!-- Count text on a trainigs -->
            <b-form-group
              content-cols="auto"
              label-for="count-options"
              :label="$t('settings.count')"
            >
              <b-form-select id="count-options" v-model="trainingSettings.count">
                <template #first>
                  <b-form-select-option :value="null">
                    {{ $t('settings.allItems') }}
                  </b-form-select-option>
                </template>
                <template #default>
                  <b-form-select-option
                    v-for="option in countOptions"
                    :value="option"
                    :key="option"
                  >
                    {{ option }}
                  </b-form-select-option>
                </template>
              </b-form-select>
            </b-form-group>
            <!-- Trainings types -->
            <b-form-group
              label-cols="auto"
              label-for="type-options"
              :label="$t('settings.type')"
            >
              <b-form-select
                class="settings-training-types"
                id="type-options"
                v-model="trainingSettings.type"
                @change="onTrainingTypeChange"
              >
                <b-form-select-option
                  v-for="action in actions"
                  :value="action"
                  :key="action"
                >
                  {{ $t(`actions.title.${action}`) }}
                </b-form-select-option>
              </b-form-select>
            </b-form-group>
            <!-- Text types -->
            <b-form-group
              content-cols="auto"
              label-for="text-types"
              :label="$t('settings.textTypes.title')"
            >
              <template #description>
                <div class="hint">
                  {{ $t('settings.textTypes.info') }}
                </div>
              </template>
              <b-form-select id="text-types" v-model="trainingSettings.textType">
                <template #first>
                  <b-form-select-option :value="null">
                    {{ $t('settings.textTypes.all') }}
                  </b-form-select-option>
                </template>
                <template #default>
                  <b-form-select-option
                    v-for="option in textTypes"
                    :value="option"
                    :key="option"
                  >
                    {{ $t(`settings.textTypes.type.${option}`) }}
                  </b-form-select-option>
                </template>
              </b-form-select>
            </b-form-group>
          </b-form-group>
          <hr/>
          <!-- Langs -->
          <b-form-group
            v-if="langs.length > 1"
            content-cols="auto"
            label-for="langs"
            :label="$t('settings.lang')"
          >
            <b-form-select
              id="langs"
              :options="getLangOptions(langs)"
              v-model="trainingSettings.lang"
            >
              <template #first>
                <b-form-select-option :value="null">
                  {{ $t('settings.allLangs') }}
                </b-form-select-option>
              </template>
            </b-form-select>
          </b-form-group>
          <!-- Native -->
          <b-form-group
            v-if="natives.length > 1"
            content-cols="auto"
            label-for="native"
            :label="$t('settings.native')"
          >
            <b-form-select
              id="natives"
              :options="getLangOptions(natives)"
              v-model="trainingSettings.native"
            />
          </b-form-group>
          <b-form-checkbox
            v-model="trainingSettings.useReverseTranslation"
            id="use-reverse-translation"
            switch
          >
            {{ $t('settings.useReverseTranslation') }}
          </b-form-checkbox>
        </b-form>
      </b-accordion-item>
      <!-- Voices -->
      <b-accordion-item
        v-show="speakVoices.langs.length"
        class="settings-section settings-voices"
      >
        <template #title>
          <div class="col-form-label-lg settings-section-title">
            {{ $t('settings.speak.title') }}
            <div class="hint">{{ $t('settings.speak.notice') }}</div>
          </div>
        </template>
        <b-form-group>
          <!-- Speak -->
          <b-form @change="onSpeakSettingsChange">
            <div class="settings-voice-enabled">
              <b-form-checkbox
                v-model="speakSettings.enabled"
                id="voices-enables"
                switch
              >
                {{ $t('settings.speak.enabled') }}
              </b-form-checkbox>
              <b-form-checkbox
                v-if="speakSettings.enabled"
                v-model="speakSettings.autoplay"
                id="voices-autoplay"
                switch
              >
                {{ $t('settings.speak.autoplay') }}
              </b-form-checkbox>
            </div>
          </b-form>
          <!-- Voices by Langs -->
          <template v-if="speakSettings.enabled">
            <template v-for="lang in speakVoices.langs" :key="lang">
              <b-form
                class="settings-voice"
                :id="lang"
                @change="onVoicesSettingsChange"
              >
                <b-form-group label-size="mg" :label="$t(`langs.${lang}`)">
                  <!-- Voices -->
                  <b-form-group
                    v-if="speakVoices[lang].length > 1"
                    label-cols="auto"
                    :label-for="`voice-${lang}-uri`"
                    :label="$t('settings.speak.voice')"
                  >
                    <b-form-select
                      :id="`voice-${lang}-uri`"
                      v-model="speakSettings[lang].voiceURI"
                    >
                      <b-form-select-option
                        v-for="voice in speakVoices[lang]"
                        :key="voice.voiceURI"
                        :value="voice.voiceURI"
                      >
                        {{ voice.lang }}  {{ voice.name }}
                      </b-form-select-option>
                    </b-form-select>
                  </b-form-group>
                  <!-- Rate -->
                  <b-form-group
                    label-cols="auto"
                    :label-for="`voice-${lang}-rate`"
                    :label="$t('settings.speak.rate')"
                  >
                    <b-form-input
                      :id="`voice-${lang}-rate`"
                      name="rate"
                      type="range"
                      min="0.5"
                      max="1.5"
                      step="0.1"
                      v-model="speakSettings[lang].rate"
                    ></b-form-input>
                  </b-form-group>
                </b-form-group>
              </b-form>
            </template>
          </template>
        </b-form-group>
      </b-accordion-item>
      <b-accordion-item class="settings-section settings-auth">
        <template #title>
          <div class="col-form-label-lg settings-section-title">
            {{ $t('auth.title') }}
          </div>
        </template>
        <yandex-auth />
        <div class="hint">
          <br>{{ $t('auth.info') }}<br></div>
      </b-accordion-item>
      <!-- Locales -->
      <b-accordion-item class="settings-section settings-ui">
        <template #title>
          <div class="col-form-label-lg settings-section-title">
            {{ $t('settings.ui.title') }}
          </div>
        </template>
        <b-form-group
          v-if="locales.length > 1"
          content-cols="auto"
          label-for="native"
          :label="$t('settings.ui.locale')"
           @change="onTrainingsSettingsChange"
        >
          <template #description>
            <div v-show="$t('autotranslated')" class="hint">
              {{ $t('autotranslated') }}
            </div>
          </template>
          <b-form-select
            id="locales"
            :options="getLangOptions(locales)"
            v-model="settings.ui.locale"
          />
        </b-form-group>
      </b-accordion-item>
    </b-accordion>
  </div>
</template>

<script>
import YandexAuth from '../../components/Yandex/Auth.vue';

export default {
  name: 'Training Settings',
  components: { YandexAuth },
  data() {
    return {
      countOptions: [5, 10, 15, 20, 25, 30],
      textTypes: ['word', 'phrase', 'sentence'],
      actions: ['card', 'picking', 'docking', 'writing', 'article'],
      locales: Object.keys(this.$i18n.messages),
    };
  },
  computed: {
    settings() {
      return this.$store.getters.settings;
    },
    trainingSettings() {
      return this.settings.training;
    },
    speakSettings() {
      return this.settings.speak;
    },
    speakVoices() {
      return this.$store.getters.voices;
    },
    allLangs() {
      return [
        ...new Set([
          ...this.$store.getters.langs,
          ...this.$store.getters.natives,
        ])
      ].sort();
    },
    natives() {
      return this.trainingSettings.useReverseTranslation
        ? this.allLangs.filter((lang) => lang !== this.trainingSettings.lang)
        : this.$store.getters.natives;
    },
    langs() {
      return this.trainingSettings.useReverseTranslation
        ? this.allLangs
        : this.$store.getters.langs;
    },
    collections() {
      return this.$store.getters.collections || [];
    },
  },
  watch: {
    'settings.updated'() {
      this.$store.dispatch('syncSettingsDB');
    },
    natives(natives) {
      let index = natives.indexOf(this.trainingSettings.native);
      if (index !== -1) return;

      index = natives.indexOf(this.settings.ui.locale);
      this.trainingSettings.native = natives[ index !== -1 ? index : 0 ];
    },
    langs(langs) {
      if (langs.length < 2) {
        this.trainingSettings.lang = null;
      }
    },
    allLangs() {
      this.$store.commit('setVoices');
    },
  },
  methods: {
    settingChange() {
      const updated = Date.now();
      this.settings.updated = updated;
      return updated;
    },
    onTrainingsSettingsChange(e) {
      this.trainingSettings.updated = this.settingChange();
    },
    onSpeakSettingsChange() {
      this.settings.speak.updated = this.settingChange();
    },
    onTrainingTypeChange(type) {
      this.$store.commit('trainingType', type);
    },
    onVoicesSettingsChange(e) {
      const lang = e.target.form.id;
      const el = e.srcElement;

      if (el.name === 'rate') {
        this.speakSettings[lang].rate = parseFloat(el.value);
      }

      this.settings.speak.updated = this.settingChange();

      this.$speak({
        text: this.settings.extra[lang]?.example,
        lang,
      });
    },
    getLangOptions(langs) {
      return langs.map((lang) => {
          return {
            text: this.$t(`langs.${lang}`),
            value: lang,
          };
      }).sort((a, b) => a.text === b.text ? 0 : a.text > b.text ? 1 : -1);
    },
  },
};
</script>
