import { createRouter, createWebHistory } from 'vue-router';
import DictionaryLayout from '../layouts/DictionaryLayout';
import TrainingLayout from '../layouts/TrainingLayout';
import Dictionary from '../views/Dictionary/Dictionary.vue';
import Training from '../views/Training/Training.vue';
import Info from '../views/Info/Info.vue';
import YandexCallback from '../components/Yandex/Callback.vue';
import DitAddDictionary from '../components/DitAddDictionary/DitAddDictionary.vue';

const type = ':type(article|card|picking|writing|docking|training)';
const routes = [
  {
    path: '/auth/yandex/callback',
    name: 'YandexCallback',
    components: {
      default: YandexCallback,
    },
    meta: {
      active: 'callback',
    },
  },
  {
    path: '/info',
    name: 'About',
    component: Info,
    meta: {
      active: 'info',
    },
  },
  {
    path: '/upload',
    name: 'Add dictionary',
    component: DitAddDictionary,
  },
  {
    path: '/dictionary',
    name: 'Saved translations',
    components: {
      default: Dictionary,
    },
    meta: {
      active: 'dictionary',
    },
  },
  {
    path: `/${type}/:lang/:text/:id?`,
    name: 'Info for dictionary select',
    component: DictionaryLayout,
    meta: {
      active: 'dictionary',
    },
    children: [
      {
        path: `/${type}/:lang/:text/:id?`,
        name: 'Training one word',
        components: {
          default: Training, // todo: other name
        },
        props: true,
      },
    ],
  },
  {
    path: '/',
    name: 'Training',
    redirect: '/training',
    component: TrainingLayout,
    meta: {
      active: 'training',
    },
    children: [
      {
        path: `/${type}`,
        name: 'Bulk training',
        component: TrainingLayout,
        props: true,
      },
    ],
  },
  // { // exclude public
  //   path: '/:pathMatch(.*)*',
  //   name: 'NotFound',
  //   redirect: '/',
  // },
  // { // norm only public/* ;(
  //   path: '/:folder*/:file.json',
  //   name: 'Static',
  //   redirect: '/',
  // },
];

const router = createRouter({
  history: createWebHistory(process.env.VUE_APP_BASE_URL),
  routes,
});

export default router;
