import axios from 'axios';

const defaultState = {
  isAuthenticated: false,
  login: null,
  email: null,
  avatar: null,
};

export default {
  namespaced: false,
  state: {
    expires_in: 30*24*60*60, // for refresh in sec // per month
    yandex: Object.assign({}, defaultState),
  },
  getters: {
    yandex: (state) => state.yandex,
  },
  mutations: {
    login(state, { name, info }) {
      state[name] = info;

      window.localStorage.setItem(`auth-${name}-info`, JSON.stringify(info));
    },
    logout(state, name) {
      state[name] = Object.assign({}, defaultState);

      window.localStorage.clear(`auth-${name}-token`);
      window.localStorage.clear(`auth-${name}-info`);
    }
  },
  actions: {
    checkAuth({ commit, state, dispatch }, name) {
      // refresh token and up info
      const expires = window.localStorage.getItem(`auth-${name}-expires`);
      if (!expires || (expires < Date.now())) {
        state[name].isAuthenticated = false;
        return dispatch('auth', name);
      }

      // set from local
      let localInfo;
      try {
        localInfo = JSON.parse(window.localStorage.getItem(`auth-${name}-info`));
      } catch (e) { return }
      if (!localInfo) return;

      const info = {};
      const defInfo = state[name];
      for (let p in defInfo) info[p] = localInfo[p];
      commit('login', { name, info });
    },

    async auth({ dispatch }, service) {
      if (service === 'yandex' || service.yandex) {
        dispatch('authYandex', service?.yandex);
      }
    },

    async authYandex({ commit, state }, code) {
      if (state.yandex.isAuthenticated) return;

      const {
        VUE_APP_YANDEX_AUTH_URL,
        VUE_APP_YANDEX_CLIENT_ID,
        VUE_APP_YANDEX_CLIENT_SECRET,
      } = process.env;

      let token = window.localStorage.getItem('auth-yandex-token');
      const getToken = async () => {
        if (!code) return;

        return axios({
          method: 'post',
          url: `${VUE_APP_YANDEX_AUTH_URL}/token`,
          data: `grant_type=authorization_code&code=${code}`,
          headers: {
            Authorization: 'Basic ' + btoa(`${VUE_APP_YANDEX_CLIENT_ID}:${VUE_APP_YANDEX_CLIENT_SECRET}`),
          },
        }).then((response) => {
          // {
          // "access_token": <xxx>,
          // "expires_in": 31536000, // sec
          // "refresh_token": <yyy>,
          // "token_type": "bearer"
          // }
          token = response.data.access_token;
          window.localStorage.setItem('auth-yandex-token', token);

          getInfo(response.data.expires_in);
        });
      };
      let tokenRetrais = 0;
      const getInfo = async (expires_in) => {
        if (!token && !tokenRetrais++) return getToken();

        return axios({
          method: 'post',
          url: 'https://login.yandex.ru/info?format=json',
          params: { // todo: delete
            oauth_token: token,
          },
          headers: { // todo: uncomment
            // Authorization: `OAuth ${token}`,
            // Authorization: `Bearer ${token}`,
          },
        }).then(function (response) {
          // {
          //   "id": <id>,
          //   "login": <login>,
          //   "client_id": <xxx>,
          //   "default_email": <email>,
          //   "emails": [ <email> ],
          //   "default_avatar_id": <string>,
          //   "is_avatar_empty": false,
          //   "psuid": <yyy>
          // }
          window.localStorage.setItem(
            'auth-yandex-expires',
            Date.now() + (expires_in > state.expires_in ? state.expires_in : expires_in)
          );

          const info = response.data;
          commit('login', {
            name: 'yandex',
            info: {
              login: info.login,
              email: info.default_email,
              avatar: `https://avatars.mds.yandex.net/get-yapic/${info.default_avatar_id}`,
              isAuthenticated: true,
            }
          });
        });
      };

      getInfo();
    },
  },
};
